@import 'reactdatetime';

.edit-form{
  padding: 30px 40px;
  font-family: $sans;

  label{
    font-size: 12px;
    font-weight: bold;
    display:block;
  }
  input{
    font-size: 16px;
    background-color: lightgrey;
    border: none;
    padding: 2px 10px;
    &:focus, &:active{
      outline: none;
    }
  }
  .input-group{
    margin-bottom: 15px;
  }

  input[name='name']{
    width: calc(100% - 40px);
    font-size: 20px;
  }
  input[name='run_date']{
    width: 140px;
  }
}
